import React from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';

//    Chart.js
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const HourUserEventsHourChart = (inData) => {


//  
    const data = inData.data;

//      
    const sortDataByHours = (data) => {
        const sortedData = {};

        for (const user in data) {
            const userData = data[user];

            //   ()     
            const sortedHours = Object.keys(userData)
                .map(Number) //    
                .sort((a, b) => a - b); //   

            //      
            sortedData[user] = {};
            sortedHours.forEach(hour => {
                sortedData[user][hour] = userData[hour];
            });
        }

        return sortedData;
    };

//  
    const sortedData = sortDataByHours(data);

//    
    const labels = Array.from({ length: 24 }, (_, i) => `${i}:00`); //    24 

    const datasets = Object.keys(sortedData).map(user => {
        const userData = sortedData[user];
        const dataForUser = labels.map(label => {
            const hour = parseInt(label.split(':')[0]); //    
            return userData[hour] || 0; //   ,  0
        });

        return {
            label: `User ${user}`,
            data: dataForUser,
            backgroundColor: `rgba(${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, 0.6)`,
        };
    });

    const chartData = {
        labels: labels, //    X ( 24 )
        datasets: datasets, //    
    };

    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: '      ',
            },
        },
        scales: {
            x: {
                title: {
                    display: true,
                    text: '',
                },
            },
            y: {
                title: {
                    display: true,
                    text: ' ',
                },
            },
        },
    };



    return <Bar data={chartData} options={options} />;
};

export default HourUserEventsHourChart;